import { DateTime } from 'luxon'

const initState = {

}


const appointmentsReducer = (state = initState, action) => {
  if (action.type === 'UPDATE_APPOINTMENT') {
    const newAppointments = {
      ...state,
    }
    newAppointments[action.id] = action.newAppointment
    return newAppointments
  }
  if (action.type === 'UPDATE_APPOINTMENTS') {
    return action.appointments
  }
  if (action.type === 'ADD_APPOINTMENT') {
    let max = 0
    for (const property in Object.keys(state)) {
      const num = parseInt(Object.keys(state)[property])
      max = (max < num) ? num : max;
    }
    const newID = max + 1
    const newAppointments = {
      ...state,
    }
    newAppointments[newID] = action.newAppointment
    return newAppointments
  }
  if (action.type === 'DELETE_APPOINTMENT') {
    let newState = state
    delete newState[action.id]
    return newState
  }

  if (action.type === 'DELETE_CLIENT') {
    let newApp = []
    Object.keys(state).forEach((id) => {
      if (state[id].client != action.id) {
        newApp.push(state[id])
      }
    })

    return newApp
  }
  return state;
}

export default appointmentsReducer


