import React, { Component } from 'react'
import { connect } from 'react-redux'
import { posY } from '../../converters/calculatePosY'
import { posX } from '../../converters/calculatePosX'
import Draggable from '../../base_componnents/draggableObject'

class Availability extends Component {
  constructor(props) {
    super(props);
    this.props = props
    this.posX = posX
    this.posY = posY
    this.state = {
      isDragging: false,
      isExtending: false,
      currentX: 0,
      currentY: 0,
      doRender: true,
    }
  }

  deleteSelf = () => {
    this.props.handleDeletingAvailability(this.props.id)
  }

  changeLengthMinutes = (lengthMinutes) => {
    this.props.handleSingleAvailabilityChange(this.props.id, {
      color: this.props.color,
      lengthMinutes: lengthMinutes,
      startTime: this.props.startTime
    })
  };

  changeStartTime = (startTime) => {
    this.props.handleSingleAvailabilityChange(this.props.id, {
      color: this.props.color,
      lengthMinutes: this.props.lengthMinutes,
      startTime: startTime
    })
  };

  render() {
    const { startTime,
      color,
      lengthMinutes,
      numDays, widthSchedule, heightSchedule, id } = this.props
    return (
      <Draggable key={"master_availability_" + id}
        numDays={numDays}
        widthSchedule={widthSchedule}
        heightSchedule={heightSchedule}
        startTime={startTime}
        color={color}
        lengthMinutes={lengthMinutes}
        changeStartTime={this.changeStartTime}
        changeLengthMinutes={this.changeLengthMinutes}
        deleteObject={this.deleteSelf}
      />
    )
  }
}

const mapStateToProps = (state) => {
  return {
    numDays: state.schedule.numDays,
    widthSchedule: state.schedule.widthSchedule,
    heightSchedule: state.schedule.heightSchedule,
  }
}

export default connect(mapStateToProps)(Availability)


