import React, { Component } from 'react'
import { connect } from 'react-redux'
import Appointment from './appointment/index'
import Available from './availablelity/index'
import './schedule.css';
import { scheduleWidthUpdate, scheduleHeightUpdate, scheduleActiveClientUpdate } from '../../actions/scheduleActions'
import { createAppointment } from '../../actions/appointmentActions'
import { position2time } from '../converters/position2time'
import ScheduleLayout from './layout'

class Schedule extends Component {
  constructor(props) {
    super(props);
    this.onResize = this.onResize.bind(this);
  }
  componentDidMount() {
    window.addEventListener("resize", this.onResize);
    this.onResize();
  }
  componentWillUnmount() {
    window.removeEventListener("resize", this.onResize);
  }
  onResize() {
    let totalWidthVar = document.getElementById('schedule-background').offsetWidth
    let totalHeightVar = document.getElementById('schedule-background').offsetHeight

    this.props.updateWidth(totalWidthVar)
    this.props.updateHeight(totalHeightVar)
  }

  onClick = ({ clientX, clientY }) => {
    if (this.props.activeClient) {
      const rect = document.getElementById("schedule-background").getBoundingClientRect();
      let x = clientX - rect.left
      let y = clientY - rect.top;
      const { startTimeWeek, numDays, totalWidth, totalHeight } = this.props
      const time = position2time(x, y, startTimeWeek, numDays, totalWidth, totalHeight)
      this.props.addAppointment(time, this.props.activeClient)
      this.props.turnOffActiveClient()
    }
  }

  render() {
    const { appointments, showAvailability, clients } = this.props
    const appointmentsObjects = Object.keys(appointments).length ? (
      Object.keys(appointments).map(appointmentID => {
        return (
          <div key={appointmentID}>
            <Appointment appointmentID={appointmentID} />
          </div>
        )
      })
    ) : (
        <div className="center"></div>
      );

    let availableObjects = null
    if (showAvailability !== null) {
      const avals = clients[showAvailability].available
      if (avals) {
        availableObjects = Object.keys(avals).map(id => {
          return (
            <div key={"available_" + id}>
              <Available appointmentID={showAvailability} id={id} />
            </div>
          )
        })
      }

    }

    return (
      <div className="row">
        <div
          className="schedule-background"
          id="schedule-background"
          key="schedule-background"
          onClick={this.onClick}
        >
          <div>{availableObjects}</div>

          <ScheduleLayout startTime={0} stopTime={24} />
          <div>{appointmentsObjects}</div>
        </div>
      </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    appointments: state.appointments,
    clients: state.clients,
    showAvailability: state.schedule.showAvailability,
    activeClient: state.schedule.activeClient,
    startTimeWeek: state.schedule.startTimeWeek,
    numDays: state.schedule.numDays,
    totalWidth: state.schedule.widthSchedule,
    totalHeight: state.schedule.heightSchedule
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    updateWidth: (newWidth) => dispatch(scheduleWidthUpdate(newWidth)),
    updateHeight: (newHeight) => dispatch(scheduleHeightUpdate(newHeight)),
    addAppointment: (startTime, clientID) => dispatch(createAppointment(startTime, clientID)),
    turnOffActiveClient: () => dispatch(scheduleActiveClientUpdate(null))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Schedule)


